#Navbar{
    height: 7.8vh;
    width: 100%;
    display: grid;
    grid-template-columns: 70px 77vw 13vw;
    color: aliceblue;
    background-color: black;
    border-bottom:  1px solid #d5c8c8;
    /* height: fit-content; */
}
#title{
    height: 60px;
}
#title h1{
    margin-top: 4px;
}
#icon{
    position: relative;
    height: 60px;
    cursor: pointer;
}
#lines{
    position: absolute;
    top: 20%;
    left: 20%;
}
.line{
    min-height: 3px;
    background-color: aliceblue;
    width: 40px;
}
.line2{
    margin-top: 8px;
    margin-bottom: 8px;
}
#log_notes{
    position: relative;
    height: 60px;
}
#log_or_notes{
    position: absolute;
    top: 25%;
    left: 45%;
}
#log_or_notes button{
    cursor: pointer;
}
#log_or_notes button:hover {
    font-size: 0.9em;
  }
@media (max-width: 440px) and (min-width:300px) {
    #Navbar{
        grid-template-columns: 15vw 63vw 13vw;
    }
}
@media (max-width: 1000px) and (min-width:450px) {
    #Navbar{
        grid-template-columns: 70px 69vw 100px;

    }
    .line{
        width: 50px;
        margin-top: 5px;
    }
    #title h1{
        margin-top: 5px;
        margin-left: 10px;
        font-size: 35px;
        /* margin-bottom: 10px; */
    }
}
@media (max-width:300px) {
    #title h1{
        margin-top: 5px;
        margin-left: 5px;
        font-size: 25px;
        /* margin-bottom: 10px; */
    } 
    #Navbar{
        grid-template-columns: 35px 50vw 80px;

    }
    .line{
        min-height: 3px;
        background-color: aliceblue;
        width: 30px;
    }
}
