.login-popup {
    position: fixed;
    top: 20%;
    left: 35%;
    width: 300px;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 999;
  }
  #close_login{
    display: grid;
    grid-template-columns:30fr 1fr;
    /* background-color: #aaa; */
  }
  #close{
    cursor: pointer;
  }
 .login-container {
    width: 100%;
    height: 100%;
    background-color: #1c2331;
    color: #fff;
    padding: 20px;
    padding-top: 0px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  
  .login-container h2 {
    margin-bottom: 10px;
  }
  
  .login-container form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
  }
  
  label {
    margin-top: 10px;
  }
  
.login_input {
    width: 100%;
    padding: 10px;
    margin-top: 5px;
    margin-bottom: 20px;
    border-radius: 5px;
    border: none;
  }
#alert div{
  color: rgb(17, 17, 17);
  font-weight: 500;
  background-color: #bc0f2e;
  width: fit-content;
  margin-top: 5px;
  border-radius: 5px;
}
  
 .login_submit {
    width: 50%;
    padding: 10px;
    margin-top: 10px;
    background-color: #4CAF50;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    border: 5px solid #828e83;
  }
  
  .or-separator {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 40px;
    color: #aaa;
  }
  
  .or-separator hr {
    width: 40%;
    height: 1px;
    background-color: #aaa;
    margin: 0 10px;
  }
.signin-btn {
    width: 80%;
    margin-left:2%;
    background-color: #1a24ae;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
    position: absolute;
    bottom: 20px;
  }

.login-container button {
  color: #fff;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.login-container button:hover {
  background-color: #0056b3;
}

.login-container button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.5);
}

.login-container button:active {
  transform: translateY(2px);
}
@media (min-width: 420px) and (max-width: 768px) {
  .login-popup{
    top: 15%;
    left: 25%;
  }
}
@media (max-width: 419px) {
  .login-popup{
    top: 15%;
    left: 10%;
    width: 250px;
    height: 400px;
  }

}
@media (max-width: 319px) {
  .login-popup{
    top: 15%;
    left: 10%;
    width: 200px;
    height: 400px;
  }

}


  
  