#Chatbar{
    height: 92vh;
    width: 100%;
    margin: 0px;
    background-image: url("/public/chatbot3.jpg");
    display: grid;
    grid-template-rows: 8fr 1fr;
    /* border:1px solid #afc810; */
}
#chat{
    overflow: auto;
}
#searchbar{
    padding: 15px;
    display: grid;
    grid-template-columns: 8fr 50px;
}
#input_search{
    background-color: rgba(240, 248, 255,0.8);
    height: 50px;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    color: rgb(17, 17, 17);
    white-space: normal;
    border: none;
    font-size: medium;
    outline: none;
}
#search-button{
    background-color: rgba(240, 248, 255,0.8);
    height: 50px;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    padding-top: 2px;
}
#search-button button {
    position: relative;
    width: 40px;
    height: 40px;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
}
  
#search-button button span {
    position: absolute;
    top: 12px;
    left: 16px;
    width: 16px;
    height: 16px;
    border: 2px solid #151414;
    border-radius: 50%;
    transform: rotate(45deg);
}
#search-button button span:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 12px;
    height: 2px;
    background: #252424;
    transform: translate(130%, -50%);
}
.Query{
    background-color: rgba(36, 40, 26, 0.8);
    color: #ede6e6;
    border: 1px solid #b1b3aa; 
    border-radius: 10px;
    min-height: 50px;
    width: 85%;
    padding: 6px;
    margin-left: 8%;
    margin-bottom: 20px;
    font-weight: 500;
    margin-top: 20px;
}
code{
    background-color: blue;
    width: 90%;
}
.Response{
    color: rgb(255, 255, 255);
    border-radius: 10px;
    width: 80%;
    margin-left: 10%;
    margin-bottom: 20px;
    background-color: rgba(7, 1, 28, 0.6);
    font-weight: 400;
    
}
