#Sidebar{
    height: 89.2vh;
    width: 280px;
    position: sticky;
    background-image: url("/public/chatbot1.jpg");
    display: grid;
    /* grid-template-rows: 80vh 4vh; */
    /* padding: 10px; */
    padding-bottom: 10px;
    padding-top: 10px;

}
.instruction{
  margin: 3vh;
  color: #141dc5;
  background-color: #987373;
  font-size: large;
}
 #Sidebar {
  border-right: 3px solid transparent;
  position: relative;
}

 #Sidebar::after {
  content: "";
  position: absolute;
  top: 0px;
  right: -3px;
  bottom: 0px;
  width: 5px;
  border-radius: 5px;
  background-color: #470404;
  box-shadow: 0 0 1px 0 #d16f6f;
  animation: glowing-border 2s ease-in-out infinite;
}

@keyframes glowing-border {
  0% {
    box-shadow: 0 0 0.2px 0 #b26767;
  }
  50% {
    box-shadow: 0 0 0.6px 1px #ad7070;
  }
  100% {
    box-shadow: 0 0 1px 1px #ad7070 }
  }
#sidebar1{
  height: 98%;
  display: grid;
  grid-template-rows: 75% 20%;
  padding: 10px;
  /* border: 10px solid red; */
}
#history{
  display: grid;
  grid-template-rows: 8fr 8fr 40fr ;
  margin-bottom: 50px;
  background-color: rgba(143, 163, 181,0.6);
}
#history h3{
  padding-left: 20px;
}
#new_topic button{
  cursor: pointer;
  height: 40px;
  width: 70%;
  font-size: medium;
  font-weight: 600;
  border-radius: 10px;
  margin-left: 20px;
  margin-top: 10px;
  background-color: rgba(252, 255, 252,0.4);
  border-width: 5px;
}
#Topics{
  overflow: auto;
}
.topic {
  background-color: gray;
  transition: transform 0.2s ease-in-out;
}
.topic:active {
  box-shadow: 0 0 10px #fff, 0 0 5px #fff, 0 0 5px #fff, 0 0 5px #7236f4, 0 0 5px  #7236f4, 0 0 5px  #7236f4, 0 0 5px  #7236f4, 0 0 5px  #7236f4;
}


.topic:hover {
  transform: scale(1.02);
}

.topic{
  margin: 10px;
  border: 1px solid #0f0c0c;
  background: linear-gradient(to right, #677bc4, #9ca2c2);
  border-radius: 5px;
  color: rgb(13, 15, 16);
  font-weight: 500;
}
#about{
  background-color: rgba(143, 163, 181,0.6);
}
#about h4 a{
  color: #271f1f;
}
::-webkit-scrollbar {
  width: 10px;
  background-color: rgba(20, 5, 183, 0.2);
}
 
::-webkit-scrollbar-thumb {
  /* background-color: rgba(20, 5, 183, 0.35); */
  background: linear-gradient(to bottom, #141dc5, #888da9);
  border-radius: 15px;
  width: 6px;
}


@media (max-width: 768px) {
    #Sidebar {
      position: fixed;
      display: none;
      /* height: 84.2vh; */
    }

  }

@media (min-width: 900px) and (max-width:1200px){
    #Sidebar{
      height: 90.4vh;
    }
  }
  